import Vue from 'vue'
import Vuex from 'vuex'
import * as types from './types.js';
import api from '../api/api.js'
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    isAuthorization: false,
    user: {},
    option_broken_line: {
      title: {
        text: "历史记录",
        textStyle: {
          fontSize: 30
        }
      },
      tooltip: {
        trigger: "axis",
      },
      legend: {
        // data: [],
        data: ["Video Ads", "Direct", "Search Engine"],
        textStyle: {
          fontSize: 26, //字体大小
          color: "#000", //字体颜色
        },
      },
      grid: {
        left: "3%",
        right: "4%",
        bottom: "3%",
        containLabel: true,
      },
      // toolbox: {
      //   feature: {
      //     saveAsImage: {}
      //   }
      // },
      xAxis: {
        type: "category",
        boundaryGap: false,
        data: ["Mon", "Tue", "Wed", "Thu", "Fri", "Sat", "Sun"],
      },
      yAxis: {
        type: "value",
      },
      series: [
        {
          name: "Video Ads",
          type: "line",
          stack: "Total",
          data: [150, 232, 201, 154, 190, 330, 410],
        },
        {
          name: "Direct",
          type: "line",
          stack: "Total",
          data: [320, 332, 301, 334, 390, 330, 320],
        },
        {
          name: "Search Engine",
          type: "line",
          stack: "Total",
          data: [820, 932, 901, 934, 1290, 1330, 1320],
        },
      ],
    },
    option_circle: {
      tooltip: {
        formatter: '{a} <br/>{b} : {c}%'
      },
      series: [
        {
          name: 'Pressure',
          type: 'gauge',
          progress: {
            show: true
          },
          detail: {
            valueAnimation: true,
            formatter: '{value}'
          },
          data: [
            {
              value: 50,
              name: 'SCORE'
            }
          ]
        }
      ]
    },
  },
  getters: {
    isAuthorization: state => state.isAuthorization,
    user: user => state.user
  },
  mutations: {
    [types.SET_AUTHORIZATION](state, isAuthorization) {
      if (isAuthorization) {
        state.isAuthorization = isAuthorization
      } else {
        isAuthorization = false
      }
    },

    [types.SET_USER](state, user) {
      if (user) {
        state.user = user
      } else {
        user = {}
      }
    },
    [types.GET_BASE_API](state, res) {
      console.log('state, res', state, res)
      state.option_broken_line.data.push(res[data])
      state.option_broken_line.series.push(res[series])
    },
    [types.GET_NEWS_LIST](state, res) {
      console.log('state, res', state, res)
      state.option_circle.series.data.push(res[data])
    }
  },
  actions: {
    setAuthorization: ({ commit }, isAuthorization) => {
      commit(types.SET_AUTHORIZATION, isAuthorization)
    },
    setsuer: ({ commit }, user) => {
      commit(types.SET_USER, user)
    },
    select_line_Data({ commit }) {
      // alert('action')
      api.select_line_Data(state.user, localStorage.getItem('token')).then(
        res => {
          console.log('action中 select_line_Data调用封装后的axios成功')
          console.log('res', res)
          console.log('action中调用封装后的axios成功')
          commit(types.GET_BASE_API, res)
        }
      )
    },
    select_tray_Data({ commit }) {
      // alert('action')
      api.select_tray_Data(state.user, localStorage.getItem('token')).then(
        res => {
          console.log('action中 select_tray_Data调用封装后的axios成功')
          console.log('res', res)
          console.log('action中调用封装后的axios成功')
          commit(types.GET_NEWS_LIST, res)
        }
      )
    },
  },
  modules: {
  }
})
